<template>
<div id="page-top">
  <!-- Page Wrapper -->
  <div id="wrapper">
    <!-- Sidebar -->
    <ul class="navbar-nav bg-gradient-primary sidebar sidebar-dark accordion" id="accordionSidebar">
      <!-- Sidebar - Brand -->
      <div class="sidebar-brand d-flex align-items-center justify-content-center">
        <div class="sidebar-brand-icon rotate-n-15">
          <i class="fas fa-laugh-wink"></i>
        </div>
        <div class="sidebar-brand-text mx-3">EBOOK</div>
      </div>

    <!-- Divider -->
      <hr class="sidebar-divider my-0">
      <li class="nav-item">
        <router-link class="nav-link" :to="{ path: '/' }">
          <i class="fas fa-fw fa-chart-area"></i>
          <span>Book Table</span>
        </router-link>
      </li>

      <!-- Divider -->
      <hr class="sidebar-divider my-0">

      <li class="nav-item">
        <router-link v-if="!ifLogin" class="nav-link" :to="{ path: '/' }">
          <i class="fas fa-fw fa-chart-area"></i>
          <span>Shop Cart</span>
        </router-link>
        <router-link v-else-if="ifLogin" class="nav-link" :to="{ path: '/cart' }">
          <i class="fas fa-fw fa-chart-area"></i>
          <span>Shop Cart</span>
        </router-link>
      </li>

      <hr class="sidebar-divider d-none d-md-block">

      <!-- Nav Item - Charts -->
      <li class="nav-item">
        <!-- <router-link class="nav-link" :to="{ path: '/login' }">
          <i class="fas fa-fw fa-chart-area"></i>
          <span>Log out</span>
        </router-link> -->
        <button class="nav-link btn btn-primary" v-on:click="handleLogin()">
          Log out
        </button>
      </li>

    </ul>
    <!-- End of Sidebar -->

    <!-- Content Wrapper -->
    <div id="content-wrapper" class="d-flex flex-column">

      <!-- Main Content -->
      <div id="content">

        <!-- Topbar -->
        <nav class="navbar navbar-expand navbar-light bg-white topbar mb-4 static-top shadow">

          <!-- Sidebar Toggle (Topbar) -->
          <button id="sidebarToggleTop" class="btn btn-link d-md-none rounded-circle mr-3">
            <i class="fa fa-bars"></i>
          </button>
        </nav>
        <!-- End of Topbar -->

        <!-- Begin Page Content -->
        <div class="container-fluid">

          <!-- Page Heading -->
          <h1 class="h3 mb-2 text-gray-800">Orders</h1>


          <!-- DataTales Example -->
          <div class="card shadow mb-4">
            <div class="card-header py-3">
              <h6 class="m-0 font-weight-bold text-primary">Unfinished Orders</h6>
            </div>
            <div class="card-body">
              <div class="table-responsive">
                <label>Filter Orders:</label>
                <input class="form-control bg-light border-0 small" v-model="filters.name.value"/>

                <div>
                  <div>
                    <v-table
                        :data="fOrderData"
                        :filters="filters"
                        selectionMode="multiple"
                        selectedClass="table-info"
                        @selectionChanged="selectedRows = $event"
                        class="table table-bordered"
                        id="dataTable"
                        width="100%"
                        cellspacing="0"
                    >
                      <thead slot="head">
                        <th>ID</th>
                        <th>BookID</th>
                        <th>BookName</th>
                        <th>Writer</th>
                        <th>Amount</th>
                        <th>Value</th>
                      </thead>
                      <tbody slot="body" slot-scope="{displayData}">
                        <v-tr
                            v-for="row in displayData"
                            :key="row.id"
                              :row="row"
                        >
                          <td>{{ row.id }}</td>
                          <td>{{ row.book.id }}</td>
                          <td>{{ row.book.name }}</td>
                          <td>{{ row.book.writer }}</td>
                          <td>{{ row.amount }}</td>
                          <td>{{ row.value }}</td>
                        </v-tr>
                      </tbody>
                    </v-table>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- DataTales Example -->
          <div class="card shadow mb-4">
            <div class="card-header py-3">
              <h6 class="m-0 font-weight-bold text-primary">Finished Orders</h6>
            </div>
            <div class="card-body">
              <div class="table-responsive">
                <label>Filter Orders:</label>
                <input class="form-control bg-light border-0 small" v-model="filters.name.value"/>

                <div>
                  <div>
                    <v-table
                        :data="uOrderData"
                        :filters="filters"
                        selectionMode="multiple"
                        selectedClass="table-info"
                        @selectionChanged="selectedRows = $event"
                        class="table table-bordered"
                        id="dataTable"
                        width="100%"
                        cellspacing="0"
                    >
                      <thead slot="head">
                        <th>ID</th>
                        <th>BookID</th>
                        <th>BookName</th>
                        <th>Writer</th>
                        <th>Amount</th>
                        <th>Value</th>
                      </thead>
                      <tbody slot="body" slot-scope="{displayData}">
                        <v-tr
                            v-for="row in displayData"
                            :key="row.id"
                            :row="row"
                        >
                          <td>{{ row.id }}</td>
                          <td>{{ row.book.id }}</td>
                          <td>{{ row.book.name }}</td>
                          <td>{{ row.book.writer }}</td>
                          <td>{{ row.amount }}</td>
                          <td>{{ row.value }}</td>
                        </v-tr>
                      </tbody>
                    </v-table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <footer class="sticky-footer bg-white">
        <div class="container my-auto">
          <div class="copyright text-center my-auto">
            <span>Copyright &copy; EBOOK Store 2019</span>
          </div>
        </div>
      </footer>

    </div>
  </div>
</div>
</template>

<script>
import http from '../../http-common.js'
import global from '../../Global'

export default {
  name: 'order',
  data () {
    return {
      useremail: '',
      loading: true,
      fOrderData: [],
      uOrderData: [],
      // currentPage: 1,
      // totalPages: 2,
      filters: {
        name: { value: '', keys: ['name', 'writer', 'price', 'isbn', 'inventory'] }
      }
    }
  },
  methods: {},
  mounted () {
    http
      .post('/order', {
        userID: this.$store.getters.get_user_id,
        finished: 1
      })
      .then(response => {
        this.fOrderData = response.data.orderItems
      }).finally(() => {
        this.loading = false
      })
      .catch(e => {
        console.log(e)
      })
    
    http
      .post('/order',{
        userID: this.$store.getters.get_user_id,
        finished: 0
      })
      .then(response => {
        this.uOrderData = response.data.orderItems
      }).finally(() => {
        this.loading = false
      })
      .catch(e => {
        console.log(e)
      })
  }
}
</script>

<style>

</style>



